import { Component } from 'preact';
import PropTypes from 'prop-types';
import cx from 'classnames';

import svgMap from './svgMap';
const typeMap = Object.keys(svgMap);
/**
 * 加载
 * @example
 * ```javascript
 * <Loading />
 * <Loading type="spinner" />
 * // ...
 * ```
 */
class Loading extends Component {
  static defaultProps = {
    prefix: 'caf-loading',
    type: 'spinner',
    vertical: false
  };
  static propTypes = {
    /**
     * 加载图标类型
     */
    type: PropTypes.oneOf(['step', 'spinner', 'ripple']),
    /**
     * 加载提示文字
     */
    text: PropTypes.string,
    /**
     * 自定义颜色
     */
    color: PropTypes.string,
    /**
     * 自定义加载图标尺寸
     */
    size: PropTypes.string
  };
  getSvg(type) {
    const { color, size } = this.props;
    return svgMap[type] && svgMap[type].func(color, size);
  }
  render({ className, prefix, type, text, vertical, ...restProps }) {
    const resultSvg = this.getSvg(type);
    return (
      !!resultSvg && (
        <div
          className={cx(prefix, className, {
            [`${prefix}__vertical`]: vertical
          })}
          {...restProps}
        >
          {resultSvg}
          {!!text && <span className={`${prefix}-text`}>{text}</span>}
        </div>
      )
    );
  }
}
export default Loading;



